@import "../../global.scss";
.portfolio {
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  h1 {
    font-size: 50px;
    overflow: hidden;
    @include mobile {
      font-size: 25px;
    }
  }
  ul {
    list-style-type: none;
    margin: 10px;
    padding: 0;
    display: flex;
    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
      margin: 10px 0;
    }
    li {
      margin-right: 50px;
      font-size: 16px;
      cursor: pointer;
      padding: 7px;
      border-radius: 10px;
      &.active {
        background-color: $mainColor;
        color: white;
      }
    }
  }
  .container {
    margin-top: 10px;
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @include mobile {
      width: 100%;
    }
    .item {
      width: 250px;
      height: 160px;
      margin: 15px 30px;
      position: relative;
      border-radius: 20px;
      color: white;
      border: 1px solid rgb(226, 223, 223);
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.5s ease;
      cursor: pointer;
      @include mobile {
        width: 140px;
        height: 120px;
        margin: 15px 10px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }
      h3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &:hover {
        background-color: $mainColor;
        img {
          z-index: 0;
          opacity: 0.3;
        }
      }
    }
  }
}
